// .photo-grid,
.pswp-gallery {
	.pswp-thumbnails {
		display: flex;
		flex-direction: row;
		max-width: 600px;
		flex-wrap: wrap;
		width: initial;
		justify-content: start;
		align-items: start;
		margin: auto;
		@media (min-width: 850px){
			max-width: 800px;
		}
		@media (max-width: 620px){
			max-width: 93vw
		}
		// @media (max-width: 480px) {
		// 	min-width: 100%;
		// 	padding: 0;
		// 	margin: 0;
		// }
		.pswp-thumbnail {
			$w: 200px;
			max-width: $w;
			max-height: $w;
			margin: 0;
			padding: 1px;
			img {
				height: 100%;
				width: 100%;
			}
			@media (max-width: 620px){
				max-width: 31vw;
				max-height: 31vw;
			}
			// @media (max-width: 480px) {
			// 	min-width: 100%;
			// 	max-width: 100%;
			// 	max-height: initial;
			// 	padding: 2rem 0;
			// 	margin: 0;
			// }
		}
	}
}

.photo-grid-wrap {
	position: relative;
	// padding-left: 0;
	// padding-right: 0;
	padding: 3rem 0;
	z-index: 8000;
}

