  
*, *:before, *:after {
	box-sizing: inherit;
}

html,body {
	height: 100%;
}

body {
	display: flex;
	flex-direction: column;
	// justify-content: space-between;
	justify-content: stretch;
	align-items: center; 
	margin: 0;
	padding: 0;
	min-height: 100vh;
	width: 100%;
	background-color: #fff;
}



#root {
	width: 100%;
	height: 100%;

	// The root element on page components. Right under #root
	& > * {
		padding: 0;
		width: 100%;
		overflow: hidden;
		// FLEX
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		flex: 1;
	}
}

main {
	padding: 0;
	width: 100%;
	overflow: hidden;
	// FLEX
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	flex: 1;
	min-height: 100vh;
	background-color: #fff;
}

article {
	padding: 6rem 1rem;
	background-color: transparent;
}


ul {
	list-style-type: none;
	margin: 0;
	padding: 0;
	display: inline-block;

	li {
		display: inline-block;
	}
}

