//============================================

// Define General Colors =====================
	$red:            $flat-red;
	$orange:         $flat-orange;
	$yellow:         $flat-yellow;
	$green:          $flat-green;
	$teal:           $flat-teal;
	$blue:           $flat-blue;
	$purple:         $flat-purple;
	$darkblue:       $flat-darkblue;

	$dark-red:       $flat-red-d;
	$dark-orange:    $flat-orange-d;
	$dark-green:     $flat-green-d;
	$dark-teal:      $flat-teal-d;
	$dark-blue:      $flat-blue-d;
	$dark-purple:    $flat-purple-d;
	$dark-darkblue:  $flat-darkblue-d;

	$light-blue:     rgba(52, 152, 219, 0.5);
	$light-yellow:   #f39c12;

// Off-Colors ================================

	$blue-grey:      #455a64;
	$dark-blue-grey: #37474f;

	// $gunmetal: #5C6370; // hsl(210,12,32); 
	$gunmetal: #4F5964;
	$dark-gunmetal: #3B4048;

// Mono ======================================
	$white:          #fff;
	$offwhite:       $flat-mono-white;
	$u-light-gray:   #d0d7dc;
	$v-light-gray:   #c9d2da;
	$light-gray:     $flat-mono-light;
	$gray:           $flat-mono;
	$dark-gray:      $flat-mono-dark;

// Background Colors =========================
	$bg-light: $offwhite;
	$bg-dark: $dark-darkblue;

// Text Colors ===============================
	$link: #08c;

	$primary:        $ideal-primary;
	$secondary:      $ideal-secondary;
	$dividers:       $ideal-dividers;
	$disabled:       $ideal-disabled;
	$hint:           $ideal-hint;
	$icon-active:    $ideal-active;
	$icon-inactive:  $ideal-inactive;

	$light-primary:   $ideal-white-primary;
	$light-secondary: $ideal-white-secondary;
	$light-dividers:  $ideal-white-dividers;
	$light-disabled:  $ideal-white-disabled;
	$light-hint:      $ideal-white-hint;
	$light-active:    $ideal-white-active;
	$light-inactive:  $ideal-white-inactive;


// Form Colors ===============================
$input-bg: $white;
$input-text: $primary;

// State Colors ===============================
$bg-active: $white;
$color-good: $green;
$color-bad: $red;

// Code Colors ===============================
$code-red: $atom-red;
$code-orange: $atom-orange;
$code-yellow: $atom-yellow;
$code-green: $atom-green;
$code-blue: $atom-blue;
$code-cyan: $atom-cyan;
$code-purple: $atom-purple;
$code-highlighted: $atom-highlighted;
$code-mono-1: $atom-mono-1;
$code-mono-2: $atom-mono-2;
$code-mono-3: $atom-mono-3;
$code-bg: $atom-bg;