@import '_importAll';

html {
	font-size: 16px;
	box-sizing: border-box;
	background-color: $offwhite;
	// font-family: $sans-serif;
	// font-family: $native-sans-serif;
	font-family: $sans-serif-mac-native;
}

#palette-root {
	main {
		// flex-direction: row;
		.ws-row {
			align-items: center;
			justify-content: center;
			flex-wrap: nowrap;
		}
	}
}
.swatch {
	$height: 50px;
	height: $height;
	line-height: $height;
	width: $height;
	max-width: $height;
	max-height: $height;
	border-radius: 5px;
	margin: 5px;
	// float: left;
	flex: 1;
	display: inline-block;
	text-align: center;
}
@for $j from 0 through 36 {
	$hue: $j * 10;
	.hue-#{$hue} {

		@for $i from 0 through 10 {
			$base-lum: 6;
			$base-color: hsl($hue,8,$base-lum);
			$factor: $i * 10;
			&.lum-#{$i} {
				background-color: lighten($base-color,$factor);
				&::before {
					content: "#{$factor + $base-lum}";
				}
			}
		}
	}
}
	